import { Component } from 'react'
import { contactService } from '../services/contactService';
import { Link } from "react-router-dom";
import { getContactById } from '../store/actions/contactActions';
import { addMove, loadUser } from '../store/actions/userActions'
import { connect } from 'react-redux';
import { TransferFund } from '../cmps/TransferFund'

class _ContactDetailsPage extends Component {
    state = {
        contactToDisplay: null,
    }
    componentDidMount() {
        this.loadContactToDisplay();
        this.props.loadUser();
        console.log(this.props)
    }
    async loadContactToDisplay() {
        const contactToDisplay = await this.props.getContactById(this.props.match.params.id)
        // const contactToDisplay = await contactService.getContactById(this.props.match.params.id);
        this.setState({ contactToDisplay });
    }
    onTransferCoins = (amount) => {
        this.props.addMove(this.state.contactToDisplay, amount)
        console.log('Transfering!');
    }
    render() {
        const { user } = this.props;
        const { contactToDisplay } = this.state;
        if (!contactToDisplay || !user) return <div>Loading...</div>
        return (
            <section className='contact-details-page'>
                <p>{contactToDisplay.name}</p>
                <p>{contactToDisplay.email}</p>
                <p>{contactToDisplay.phone}</p>
                <img src={`https://robohash.org/${contactToDisplay._id}?set=set2`} alt="" />
                <TransferFund onTransferCoins={this.onTransferCoins} maxCoins={this.props.user.coins} name={contactToDisplay.name} ></TransferFund>
                <div className='navigation'>
                    <Link to={`/contact`}>Back</Link>
                    <Link to={`/contact/edit/${contactToDisplay._id}`}>Edit</Link>
                </div>
            </section >
        )
    }
    // contact, maxCoins, onTransferCoins
}
const mapStateToProps = state => {
    return {
        user: state.userModule.loggedInUser,
    }
}

const mapDispatchToProps = {
    getContactById,
    addMove,
    loadUser
}

export const ContactDetailsPage = connect(mapStateToProps, mapDispatchToProps)(_ContactDetailsPage)

