import { Component } from 'react';
import { connect } from 'react-redux';
import { contactService } from '../services/contactService.js';
import { ContactList } from '../cmps/ContactList.jsx';
import { ContactFilter } from '../cmps/ContactFilter.jsx';
import { loadContacts, setFilterBy } from '../store/actions/contactActions.js';
import { loadUser } from '../store/actions/userActions.js';
import { MovesList } from "../cmps/MovesList.jsx"


class _ContactPage extends Component {

    componentDidMount() {
        // this.loadContacts(this.state.filterBy);
        this.props.loadContacts()
        this.props.loadUser();
    }
    async loadContacts() {
        const contacts = await contactService.getContacts(this.state.filterBy);
        this.setState({ contacts });
    }
    onChangeFilter = (filterBy) => {
        // console.log('setting:', filterBy)
        // this.setState({ filterBy }, this.loadContacts);
        this.props.setFilterBy(filterBy)
        this.props.loadContacts()
    }
    render() {
        // const { contacts } = this.state;
        const { contacts, user } = this.props;
        // const { user } = this.props;
        if (!contacts, user) return <div>Loading...</div>
        return (
            <section className='contact-page'>
                <h1>Avaliable Contacts</h1>
                <ContactFilter onChangeFilter={this.onChangeFilter}></ContactFilter>
                <div className='lists'>
                    <MovesList moves={user.moves}></MovesList>
                    <ContactList contacts={contacts}></ContactList>
                </div>
            </section>
        )
    }
}

const mapStateToProps = state => {
    return {
        contacts: state.contactModule.contacts,
        user: state.userModule.loggedInUser
    }
}

const mapDispatchToProps = {
    loadContacts,
    setFilterBy,
    loadUser,
}

export const ContactPage = connect(mapStateToProps, mapDispatchToProps)(_ContactPage)