
import { Link } from "react-router-dom"


export function MovesList({ moves }) {
    return (
        <section className="moves-list">
            <div className='list-container'>
                <h3>Recent Coins Transfers:</h3>
                <ul>
                    {moves.map(move =>
                        <div key={'k' + move.id} >
                            {/* <li>To: {move.to} |  Amount: {move.amount} Coins </li> */}
                            <li>{move.amount} Coins to {move.to} </li>
                        </div>
                    )}
                </ul>
            </div>

        </section >
    )
}