const INITIAL_STATE = {
    loggedInUser: null,
}

export function userReducer(state = INITIAL_STATE, action) {

    switch (action.type) {
        case 'SET_USER':
        case 'SIGNUP':
            return {
                ...state,
                loggedInUser: action.user
            }
        case 'ADD_MOVE':
            const { loggedInUser } = state
            return {
                ...state,
                loggedInUser: { ...loggedInUser, coins: action.user.coins, moves: action.user.moves }
            }
        default:
            return state;
    }
}