
import { Component } from 'react'
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import { ContactPage } from './ContactPage.jsx';
import { HomePage } from './HomePage.jsx';
import { StatisticPage } from './StatisticPage.jsx';
import { ContactDetailsPage } from './ContactDetailsPage.jsx';
import { getSpaceUntilMaxLength } from '@testing-library/user-event/dist/utils';
import { ContactEditPage } from './ContactEditPage.jsx';
import { AppHeader } from '../cmps/AppHeader.jsx';
import { SignupPage } from './SignupPage.jsx';

export class ContactApp extends Component {

    render() {
        return (
            <Router>
                <div className='app-container'>
                    <AppHeader />
                    <main>
                        <Switch>
                            <Route component={ContactEditPage} path='/contact/edit/:id?' />
                            <Route component={ContactDetailsPage} path='/contact/:id' />
                            <Route component={ContactPage} path='/contact' />
                            <Route component={StatisticPage} path='/chart' />
                            <Route component={SignupPage} path='/signup' />
                            <Route component={HomePage} path='/' />
                        </Switch>
                    </main>
                </div>
            </Router>
        )
    }




}
