import { NavLink, withRouter } from "react-router-dom";

export function AppHeader() {
    return (
        <header>
            <NavLink activeClassName="my-active" exact to='/'>Home</NavLink>
            <NavLink activeClassName="my-active" to='/contact'>List</NavLink>
            <NavLink activeClassName="my-active" exact to='/chart'>Statistics</NavLink>
        </header>
    )
}