
// Use axios to fetch the data.
// Functions:
//  getRate(coins) (returns Promise)
//  Bitcoin rate (use a Bitcoin value API such as this)
//  getMarketPrice(),
//  getConfirmedTransactions()
//  Return chart data as described below.

import axios from "axios";


export const bitcoinService = {
    getRate,
    getMarketPrice,
    getConfirmedTransactions,
}

async function getRate(coins) {
    try {
        return await axios.get(`https://blockchain.info/tobtc?currency=USD&value=${coins}`)
    } catch (err) {
        console.log(err);
    }
}

async function getMarketPrice() {
    try {
        return await axios.get('https://api.blockchain.info/charts/market-price?timespan=5months&format=json&cors=true')
    } catch (err) {
        console.log(err);
    }

}

async function getConfirmedTransactions() {
    try {
        return await axios.get('https://api.blockchain.info/charts/trade-volume?timespan=5months&format=json&cors=true')
    } catch (err) {
        console.log(err);
    }
}

