import { contactService } from "../../services/contactService.js";


export function loadContacts() {
    return async (dispatch, getState) => {
        const { filterBy } = getState().contactModule
        try {
            const contacts = await contactService.getContacts(filterBy)
            dispatch({ type: 'SET_CONTACTS', contacts })
        } catch (err) {
            console.log(err);
        }
    }
}

export function saveContact(contact) {
    return async (dispatch) => {
        try {
            const newContact = await contactService.saveContact(contact)
            contact._id ? dispatch({ type: 'UPDATE_CONTACT', contact: newContact }) : dispatch({ type: 'SAVE_CONTACT', contact: newContact });
        } catch (err) {
            console.log(err);
        }
    }
}

export function removeContact(contactId) {
    return async (dispatch) => {
        try {
            await contactService.deleteContact(contactId)
            dispatch({ type: 'REMOVE_CONTACT', contactId })
        } catch (err) {
            console.log(err);
        }
    }
}

export function setFilterBy(filterBy) {
    return async (dispatch) => {
        dispatch({ type: 'SET_FILTER_BY', filterBy })
    }
}

export function getContactById(contactId) {
    return async () => {
        return await contactService.getContactById(contactId)
    }
}

