// Use UserService.getUser and BitcoinService and display:
//  User Name and Coins
//  Current Bitcoin rat

import { Component } from 'react'
import { userService } from '../services/userService.js';
import { bitcoinService } from '../services/bitcoinService.js';
import { connect } from 'react-redux';
import { loadUser } from '../store/actions/userActions.js';
import { MovesList } from '../cmps/MovesList.jsx';

class _HomePage extends Component {
    state = {
        user: null,
        coinsValue: null,
    }

    componentDidMount() {
        this.props.loadUser();
        // if (!this.props.user) {
        //     this.props.history.push('/signup')
        //     return
        // }
        this.loadUser();
    }

    async loadUser() {
        const user = userService.getUser();
        if (!user) {
            this.props.history.push('/signup')
            return
        }
        this.setState({ user });
        try {
            const res = await bitcoinService.getRate(user.coins);
            this.setState({ coinsValue: res.data });
        } catch (err) {
            console.log(err);
        }
    }


    render() {
        const { coinsValue } = this.state;
        const { user } = this.props
        if (!user) return <div>Loading...</div>
        return (
            <div className='home-page'>
                <h1>Welcome {user.name}</h1>
                <p>coins: {user.coins}</p>
                <p>Current Rate: 1$ = {coinsValue} Coins</p>
                <img src={`https://robohash.org/${user._id}?set=set2`} alt="" />
                {/* <MovesList moves={user.moves}></MovesList> */}
                {/* <p>Actions history: {user.moves || 'No Recent Moves'}</p> */}

            </div>
        )
    }

}

const mapStateToProps = state => {
    return {
        user: state.userModule.loggedInUser,
    }
}

const mapDispatchToProps = {
    loadUser,
}

export const HomePage = connect(mapStateToProps, mapDispatchToProps)(_HomePage)