

import { Component } from 'react'
import { bitcoinService } from '../services/bitcoinService.js';
import { Chart } from '../cmps/Chart.jsx';

export class StatisticPage extends Component {
    state = {
        pricesData: null,
        tradeData: null,
    }
    componentDidMount() {
        this.loadPriceData();
        this.loadTradeDate();
    }
    getDataToShow(dataArr) {
        const chartsData = dataArr.map(item => item.y)
        return chartsData;
    }
    async loadPriceData() {
        const res = await bitcoinService.getMarketPrice();
        console.log(res.data)

        this.setState({ pricesData: res.data.values });
    }
    async loadTradeDate() {
        const res = await bitcoinService.getConfirmedTransactions();
        console.log(res.data)
        this.setState({ tradeData: res.data.values });
    }
    render() {
        const { tradeData, pricesData } = this.state;
        if (!tradeData || !pricesData) return <div>Loading...</div>
        return (
            <section className='statistic-page'>
                <div className='stats-charts'>
                    <h1>Market Price:</h1>
                    <Chart data={this.getDataToShow(pricesData)}></Chart>
                    <h1>Trade Volume:</h1>
                    <Chart data={this.getDataToShow(tradeData)}></Chart>
                </div>
            </section>
        )
    }

}

