import { Component } from 'react'
import { Link } from "react-router-dom"
import { connect } from 'react-redux';
import { contactService } from '../services/contactService';
import { saveContact, removeContact, getContactById } from '../store/actions/contactActions.js';

// Allows Adding and Editing a contact
//  Gets a contact from the service by id or start with a new contact
//  Allow editing the name, email and phone of that contact
//  CREATE MODE: EDIT MODE:
//  Add action buttons:
// Back  back to contact details
// Delete  remove the contact and navigate to <ContactPage>

class _ContactEditPage extends Component {
    state = {
        contactToEdit: null,
    }
    componentDidMount() {
        this.loadContactToEdit();
    }
    handleChanges = (ev) => {
        // console.log(ev);
        const field = ev.target.name;
        const value = ev.target.type === 'number' ? +ev.target.value : ev.target.value
        this.setState(prevState => ({ contactToEdit: { ...prevState.contactToEdit, [field]: value } }))
    }
    saveChanges = async (ev) => {
        ev.preventDefault()
        // await contactService.saveContact(this.state.contactToEdit);

        await this.props.saveContact(this.state.contactToEdit)
        this.props.history.push('/contact')
    }
    removeContact = async () => {
        // await contactService.deleteContact(this.state.contactToEdit._id);

        await this.props.removeContact(this.state.contactToEdit._id)
        this.props.history.push('/contact')
    }
    getLink() {
        const { contactToEdit } = this.state
        return contactToEdit._id ? contactToEdit._id : ''
    }
    async loadContactToEdit() {
        const contactId = this.props.match.params.id;
        const contactToEdit = contactId ? await this.props.getContactById(contactId) : contactService.getEmptyContact();

        // const contactToEdit = contactId ? await contactService.getContactById(contactId) : contactService.getEmptyContact();
        this.setState({ contactToEdit });
    }
    render() {
        const { contactToEdit } = this.state;
        if (!contactToEdit) return <div>Loading...</div>
        return (
            <section className='contact-edit-page'>
                <div className='actions'>
                    <Link to={`/contact/${this.getLink()}`}>Back</Link>
                    <button onClick={this.removeContact}></button>
                </div>
                <form onSubmit={this.saveChanges} >
                    <label htmlFor="name">Name</label>
                    <input onChange={this.handleChanges} value={contactToEdit.name} type="text" name="name" id="name" />
                    <label htmlFor="phone">Phone</label>
                    <input onChange={this.handleChanges} value={contactToEdit.phone} type="text" name="phone" id="phone" />
                    <label htmlFor="email">Email</label>
                    <input onChange={this.handleChanges} value={contactToEdit.email} type="text" name="email" id="email" />
                    <button >Save</button>

                </form>
            </section>
        )
    }
}



const mapDispatchToProps = {
    removeContact,
    saveContact,
    getContactById,
}

export const ContactEditPage = connect(undefined, mapDispatchToProps)(_ContactEditPage)