import { Link } from "react-router-dom"


export function ContactPreview({ contact }) {
    return (
        <section className="contact-preview">
            <p>{contact.name}</p>
            <Link to={`/contact/${contact._id}`}>Details</Link>
            <img src={`https://robohash.org/${contact._id}?set=set2`} alt="" />
            
        </section>
    )
}
