// Ask for user name and save the new user in local storage and local variable using 
// the UserService.
//  When user is not known we route to this page
//  The <SignupPage> just requests a name
//  New user gets 100 coins when signup
//  To keep it simple, do the signup process synchronously (no need for 
// promises here in UserService) 




export const userService = {
    getUser,
    signup,
    addMove,
}

var LoggedUser;
const KEY = 'LoggedUser';

function getUser() {
    // return {
    //     name: "Ochoa Hyde",
    //     coins: 100,
    //     moves: []
    // }
    const user = loadFromStorage(KEY);
    LoggedUser = user;
    return user;
}

function signup(name) {
    const user = {
        name,
        coins: 100,
        moves: [],
    }
    saveToStorage(KEY, user)
    LoggedUser = user;
}

function addMove(contact, amount) {
    LoggedUser.coins -= amount;
    LoggedUser.moves.push({ id: _makeId(), to: contact.name, amount });
    console.log(LoggedUser);
    saveToStorage(KEY, LoggedUser);
}

function saveToStorage(key, value) {
    localStorage.setItem(key, JSON.stringify(value) || null);
}

function loadFromStorage(key) {
    let data = localStorage.getItem(key);
    return (data) ? JSON.parse(data) : undefined;
}

function _makeId(length = 10) {
    var txt = ''
    var possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'
    for (var i = 0; i < length; i++) {
        txt += possible.charAt(Math.floor(Math.random() * possible.length))
    }
    return txt
}