import { Component } from 'react'
import { Link } from "react-router-dom"


export class TransferFund extends Component {

    state = {
        amount: 0,
        showMsg: null,
    }

    handleChange = ({ target }) => {
        const value = target.type === 'number' ? +target.value : target.value
        // if (value > this.props.maxCoins) return; // watchout
        this.setState({ amount: value })
    }
    checkBalance = (ev) => {
        ev.preventDefault();
        if (this.state.amount > this.props.maxCoins) {
            console.log('not enough!');
            this.setState({ showMsg: 'Error: check your input and your balance please!' })
            this.clearMsg();
            return;
        }
        else if (!this.state.amount) {
            return;
        }
        this.props.onTransferCoins(this.state.amount);
    }
    clearMsg() {
        setTimeout(() => {
            this.setState({ showMsg: '' })
        }, 3000);
    }
    getTxt() {
        if (this.showMsg) return 'Error check your input and your balance please!'
        return ''
    }
    render() {
        const { amount, showMsg } = this.state
        return (
            <form className='transfer-fund' onSubmit={this.checkBalance}>

                <h3>Make a coin transfer to {this.props.name}</h3>
                <section className='fund-input'>
                    <label htmlFor="amount">Amount:</label>
                    <input onChange={this.handleChange} value={amount} type="number" name="amount" id="amount" />
                    <p className='errorMsg'>{showMsg}</p>
                    <button>Send</button>
                    {/* <Link to={`/contact/edit/`}>Add</Link> */}
                </section>
            </form>
        )
    }
}